

import { defineComponent, onMounted } from 'vue'
import "swiper/dist/css/swiper.css";
import Swiper from "swiper";
interface IntroListTs {
  name: string,
  icon: string,
  cont?: string,
  url?: string
}

interface ConditionsTs {
  name: string,
  cont: string
}


export default defineComponent({
  components: {},
  setup () {
    const introList: Array<IntroListTs> = [{
      name: '智慧空间格调',
      icon: require('../images/advantage-1.png'),
    }, {
      name: '高口碑式运营',
      icon: require('../images/advantage-2.png'),
    }, {
      name: '自有会员体系',
      icon: require('../images/advantage-3.png'),
    }, {
      name: '裂变式社群营销',
      icon: require('../images/advantage-4.png'),
    }]
    const conditions: Array<ConditionsTs> = [{
      name: '建筑面积及房量',
      cont:  `<p>18°居：3500-10000㎡/100间以上
              <br/>栖语酒店：3000-5000㎡/80间以上
              <br/>似栖酒店：1000-5000㎡/30间以上</p>`
    },{
      name: '客房面积',
      cont:  `<p>18°居：20-36㎡
              <br/>栖语酒店：18-25㎡
              <br/>似栖酒店：15-20㎡</p>`
    },{
      name: '独立大堂面积',
      cont:  `<p>18°居：200-300㎡
              <br/>栖语酒店：100-300㎡
              <br/>似栖酒店：100㎡</p>`
    }]

    onMounted(() => {
      new Swiper(".swiper-container", {
        direction: "vertical",
        mousewheel: true,
        forceToAxis: true,
        keyboard: {
          enabled: true,
          onlyInViewport: false,
        },
        observer: true, // 修改swiper自己或子元素时，自动初始化swiper
        observeParents: true, // 修改swiper的父元素时，自动初始化swiper
      });
    });

    return {
      introList,
      conditions
    }
  }
})
